import React, { useEffect } from 'react';
import {
  Container,
  Typography,
  Box,
  Card,
  CardMedia,
  Button
} from '@mui/material';
import { useNavigate } from 'react-router-dom';
import { motion } from 'framer-motion';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';

import floatProduct from '../assets/float-product.png';
import img1 from '../assets/images/whatsapp-image-2025-06-14.jpg';
import img2 from '../assets/images/screenshot-2025-06-14-072257.png';
import img3 from '../assets/images/screenshot-2025-06-14-072340.png';

const Home = () => {
  const navigate = useNavigate();

  // Bubble animation keyframes
  const bubbleKeyframes = `
    @keyframes bubbleFloat {
      0% {
        transform: translateY(0) scale(1);
        opacity: 0.6;
      }
      50% {
        transform: translateY(-100px) scale(1.2);
        opacity: 0.8;
      }
      100% {
        transform: translateY(-200px) scale(0.8);
        opacity: 0;
      }
    }
  `;

  useEffect(() => {
    const observer = new IntersectionObserver((entries) => {
      entries.forEach(entry => {
        if (entry.isIntersecting) {
          entry.target.style.opacity = '1';
          entry.target.style.transform = 'translateY(0)';
        }
      });
    }, { threshold: 0.1 });

    const elements = document.querySelectorAll('.fade-in');
    elements.forEach(el => observer.observe(el));

    return () => elements.forEach(el => observer.unobserve(el));
  }, []);

  return (
    <Box>
      {/* Inject keyframes */}
      <style>{bubbleKeyframes}</style>

      {/* Hero Section */}
      <Box
        sx={{
          minHeight: '90vh',
          background: 'linear-gradient(135deg, #00bcd4, #0097a7)',
          display: 'flex',
          flexDirection: 'column',
          justifyContent: 'center',
          alignItems: 'center',
          color: 'white',
          textAlign: 'center',
          px: 3,
          position: 'relative',
          overflow: 'hidden'
        }}
      >
        {[...Array(15)].map((_, i) => (
          <Box
            key={i}
            sx={{
              position: 'absolute',
              bottom: '-40px',
              left: `${Math.random() * 100}%`,
              width: '12px',
              height: '12px',
              backgroundColor: 'rgba(255,255,255,0.6)',
              borderRadius: '50%',
              animation: 'bubbleFloat 6s ease-in infinite',
              animationDelay: `${Math.random() * 5}s`,
              animationDuration: `${3 + Math.random() * 5}s`,
            }}
          />
        ))}

        <motion.div initial={{ opacity: 0, y: 30 }} animate={{ opacity: 1, y: 0 }} transition={{ duration: 1 }}>
          <Typography variant="h2" gutterBottom>
            Welcome to AquaChilly
          </Typography>
          <Typography variant="h6" sx={{ maxWidth: 600, mx: 'auto', mb: 4 }}>
            Experience the ultimate in floating luxury with our exclusive premium float products.
          </Typography>
          <Button
            variant="contained"
            size="large"
            onClick={() => navigate('/products')}
            sx={{
              px: 5,
              py: 1.5,
              borderRadius: 3,
              backgroundColor: '#ffffff',
              color: '#0097a7',
              fontWeight: 'bold',
              textTransform: 'none',
              '&:hover': {
                backgroundColor: '#e0f7fa'
              }
            }}
          >
            Explore Products
          </Button>
        </motion.div>
      </Box>

      {/* Product Carousel Section */}
      <Container sx={{ my: 6 }}>
        <Typography variant="h4" align="center" gutterBottom>
          Featured Floats
        </Typography>
        <Swiper spaceBetween={20} slidesPerView={1.2} loop={true}>
          {[img1, img2, img3].map((img, i) => (
            <SwiperSlide key={i}>
              <motion.div
                className="fade-in"
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.8, delay: i * 0.2 }}
              >
                <Card
                  sx={{
                    maxWidth: 400,
                    mx: 'auto',
                    borderRadius: 3,
                    overflow: 'hidden',
                    boxShadow: 3,
                    transition: 'transform 0.3s ease',
                    '&:hover': {
                      transform: 'scale(1.05)'
                    }
                  }}
                >
                  <CardMedia component="img" height="300" image={img} alt={`Product ${i + 1}`} />
                </Card>
              </motion.div>
            </SwiperSlide>
          ))}
        </Swiper>
      </Container>

      {/* Highlight Section */}
      <Container sx={{ my: 8 }}>
        <Box
          sx={{
            display: 'flex',
            flexDirection: { xs: 'column', md: 'row' },
            alignItems: 'center',
            gap: 4
          }}
        >
          <motion.div
            className="fade-in"
            initial={{ opacity: 0, x: -30 }}
            whileInView={{ opacity: 1, x: 0 }}
            transition={{ duration: 0.8 }}
          >
            <Card sx={{ maxWidth: 500, borderRadius: 3 }}>
              <CardMedia
                component="img"
                height="400"
                image={floatProduct}
                alt="Float Product"
                sx={{ objectFit: 'cover' }}
              />
            </Card>
          </motion.div>

          <motion.div
            className="fade-in"
            initial={{ opacity: 0, x: 30 }}
            whileInView={{ opacity: 1, x: 0 }}
            transition={{ duration: 0.8 }}
          >
            <Typography variant="h4" gutterBottom>
              Float in Style
            </Typography>
            <Typography paragraph>
              Crafted with comfort and elegance, our float products are the perfect blend of form and function.
            </Typography>
            <Typography paragraph>
              Whether it's a pool party or a quiet beach day, AquaChilly gives you the best floating experience.
            </Typography>
            <Button
              variant="outlined"
              onClick={() => navigate('/products')}
              sx={{ mt: 2, textTransform: 'none', borderRadius: 2 }}
            >
              Learn More
            </Button>
          </motion.div>
        </Box>
      </Container>
    </Box>
  );
};

export default Home;
